import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  // Vuex
  state: {
    isLoading: false,
    homeLoading: false,
    products: [],
    filterTodos: [],
  },
  actions: {
    //payload (status)
    updateLoading(context, status) {
      context.commit('LOADING', status);
      //contextmutaions
    },
    updateHomeLoading(context, status) {
      context.commit('HOMELOADING', status);
    },
    getProducts(context) {
      const url = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
        }/products/all`;
      context.commit('LOADING', true);
      axios.get(url).then(res => {
        context.commit('PRODUCTS', res.data.products);
        context.commit('CATEGORIES', res.data.products);
        context.commit('LOADING', false);
        console.log(res);
      });
    },
  },
  // settimeout or Ajax actions
  mutations: {
    LOADING(state, status) {
      state.isLoading = status;
      // LOADINGstatestatus
    },
    HOMELOADING(state, status) {
      state.homeLoading = status;
    },
    // PRODUCTS(state, payload) {
    //  state.products = payload
    // },
    // CATEGORIES(state, payload) {
    //     let newAry = [];
    //     if (type == "") {
    //       vm.filterTodos = payload;
    //     } else {
    //       newAry = payload.filter(function(product) {
    //         return product.category == type;
    //       });
    //       state.filterTodos = newAry;
    //     }
    // }
  },
  //
})


